import * as React from "react";
import {
  Card,
  CardBody,
  CardHeader,
  CardLink,
  CardText,
  CardTitle,
} from "reactstrap";
import { BlogData } from "./Data/BlogData";
import BlogModel from "./Models/BlogModel";

export default class Blogs extends React.Component<{}, { blogs: BlogModel[] }> {
  constructor(props) {
    super(props);
    this.state = { blogs: Array<BlogModel>() };
  }

  async componentDidMount() {
    //let response = await fetch('Data/BlogData.json');
    // let blogs: BlogModel[] = await response.json();
    let blogs: BlogModel[] = JSON.parse(BlogData);
    this.setState({ blogs });
  }

  render() {
    return (
      <section id="Blogs" style={{ position: "relative" }}>
        <div className="centered">
          <h1 className="h2 mb-4">My Blogs</h1>

          <div className="blogs">
            {this.state.blogs.map((blog) => {
              return (
                <Card className="text-white bg-dark" key={blog.ID}>
                  <CardHeader>
                    <CardTitle className="blobTitle">{blog.title}</CardTitle>
                  </CardHeader>
                  <CardBody>
                    <CardText>{blog.content}</CardText>
                    <CardLink
                      href={blog.blogURL}
                      rel="noreferrer noopener"
                      target="blank"
                    >
                      Read more...
                    </CardLink>
                  </CardBody>
                </Card>
              );
            })}
          </div>
        </div>
        <div className="wave">
          <svg
            data-name="Layer 1"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 1200 120"
            preserveAspectRatio="none"
          >
            <path
              d="M985.66,92.83C906.67,72,823.78,31,743.84,14.19c-82.26-17.34-168.06-16.33-250.45.39-57.84,11.73-114,31.07-172,41.86A600.21,600.21,0,0,1,0,27.35V120H1200V95.8C1132.19,118.92,1055.71,111.31,985.66,92.83Z"
              className="shape-fill"
            ></path>
          </svg>
        </div>
      </section>
    );
  }
}
